body {
  font-family: Acme, sans-serif;
}

.search-form {
  height: 50px;
  background-color: #00008b;
  outline: 1px solid #000;
  justify-content: center;
  display: flex;
  position: relative;
}

.search-form input {
  width: 200px;
  border-radius: 4px;
  margin-top: 8px;
  margin-bottom: 8px;
  font-family: inherit;
  font-size: 20px;
}

.search-form button {
  cursor: pointer;
  border-radius: 4px;
  margin-top: 8px;
  margin-bottom: 8px;
  position: absolute;
  left: 53.7%;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 20px;
  display: flex;
}

.photo-card {
  color: inherit;
  flex-basis: calc(25% - 15px);
  text-decoration: none;
  box-shadow: 4px 4px 4px gray;
}

.info {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 15px;
  display: flex;
}

.info-item {
  flex-wrap: wrap;
  flex-basis: 54px;
  justify-content: center;
  display: flex;
}

.info-item div {
  width: 50%;
}

img {
  width: 100%;
  height: 250px;
  object-fit: cover;
  display: block;
}

